import { DocumentUser } from ".";

export interface PositionAPI {
	courses: {
		course_name: string;
		course_code: string;
		description: string;
		school_levels: string[];
		course_id: string;
	}[];
	position_title: string;
	description: string;
	school_level_id: {
		school_level_name: string;
		description: string;
		education_institute: string[];
		courses: [],
		school_level_id: string;
	};
	education_institute_id: {
		education_category_title: string,
		school_levels: [],
		description: string,
		education_institute_id: string;
	};
	requirements: PositionRequirements[],
	position_id: string;
}

export interface PositionRequirements {
	user_detail_category_id: string;
	title: string;
	description: string;
	fields: string[];
	user_required_docs: {
		category_id: string;
		description: string;
		title: string;
		user_detail_subcategory_id: string;
	}[];
} 

export const validateRequirement = (requirement: PositionRequirements, userDocuments: DocumentUser[]) => {
	// List of invalid subCategories
	let result: {
		category_id: string | null;
		user_detail_subcategory_id: string | null;
	}[] = [];
	if (userDocuments.find(item => item.category_id === requirement.user_detail_category_id)?.sub_category_id !== undefined) {
		if (userDocuments.find(item => item.category_id === requirement.user_detail_category_id)?.category_id !== undefined) {
			result.push({category_id: requirement.user_detail_category_id, user_detail_subcategory_id : null});
		}
	} else {
		for (let i in requirement.user_required_docs) {
			if (userDocuments.find(item => item.sub_category_id === requirement.user_required_docs[i].user_detail_subcategory_id) === undefined) {
				if (requirement.user_required_docs[i].user_detail_subcategory_id) {
					result.push({category_id: requirement.user_detail_category_id, user_detail_subcategory_id : requirement.user_required_docs[i].user_detail_subcategory_id});
				}
			}
		}
	}
	return result;
}

