export const validateDocCategory = (requirement: PositionRequirements, userDocuments: DocumentUser[]) => {
	if (userDocuments.find(item => item.category_id === requirement.user_detail_category_id)?.category_id === undefined) {
		return requirement.user_detail_category_id;
	} else {
		return null;
	}
}

export const validateDocSubcategory = (requirement: PositionRequirements, userDocuments: DocumentUser[]) => {
	let result: {
		category_id: string | null;
		user_detail_subcategory_id: string | null;
	}[] = [];
	if (requirement.user_required_docs.length > 0) {
		for (let i in requirement.user_required_docs) {
			if (userDocuments.find(item => item.sub_category_id === requirement.user_required_docs[i].user_detail_subcategory_id)?.sub_category_id === undefined) {
				result.push({
					category_id: requirement.user_required_docs[i].category_id,
					user_detail_subcategory_id: requirement.user_required_docs[i].user_detail_subcategory_id
				});
			}
		}
	}
	console.log("Res size: ", result.length);
	return result;
}

export const returnValidatePosition = (requirements: PositionRequirements[], documents: DocumentUser[]) => {
	let status = true;
	for (let i in requirements) {
		if (requirements[i].user_required_docs.length === 0) {
			if (requirements[i] !== null && documents !== null && validateDocCategory(requirements[i], documents) !== null && validateDocCategory(requirements[i], documents) !== undefined) {
				status = false;
			}
		} else {
			if (validateDocSubcategory(requirements[i], documents).length !== 0) {
				status = false;
			}
		}
	}
	return status;
}